  document.addEventListener('DOMContentLoaded', function() {
    var CloseElements = document.querySelectorAll("#waiter_close");
    var waiterElements = document.querySelectorAll("#waiter");
    var MenuElements = document.querySelectorAll("#waiter_menu");
    var button_call_waiter = document.querySelectorAll('.button_call_waiter');
    var button_score = document.querySelectorAll('.button_score');
    
    waiterElements.forEach(function(waiter) {
      waiter.addEventListener('click', function() {
        MenuElements.forEach(function(menu) {
          menu.style.display = 'block';
        });
        waiterElements.forEach(function(w) {
          w.style.display = 'none';
        });
        CloseElements.forEach(function(close) {
          close.style.display = 'block';
        });
      });
    });
    
    CloseElements.forEach(function(close) {
      close.addEventListener('click', function() {
        MenuElements.forEach(function(menu) {
          menu.style.display = 'none';
        });
        CloseElements.forEach(function(c) {
          c.style.display = 'none';
        });
        waiterElements.forEach(function(w) {
          w.style.display = 'block';
        });
      });
    });

    button_call_waiter.forEach(function(close) {
      close.addEventListener('click', function(event) {
        event.preventDefault();
        MenuElements.forEach(function(menu) {
          menu.style.display = 'none';
        });
        CloseElements.forEach(function(c) {
          c.style.display = 'none';
        });
        waiterElements.forEach(function(w) {
          w.style.display = 'block';
        });
      });
    });

    button_score.forEach(function(close) {
      close.addEventListener('click', function(event) {
        event.preventDefault();
        MenuElements.forEach(function(menu) {
          menu.style.display = 'none';
        });
        CloseElements.forEach(function(c) {
          c.style.display = 'none';
        });
        waiterElements.forEach(function(w) {
          w.style.display = 'block';
        });
      });
    });
  
    // Обработчик событий для закрытия всплывающих кнопок при клике на любом другом элементе страницы
    document.addEventListener('click', function(event) {
      if (!event.target.closest("#waiter_menu") && !event.target.closest("#waiter") && !event.target.closest("#waiter_close")) {
        MenuElements.forEach(function(menu) {
          menu.style.display = 'none';
        });
        CloseElements.forEach(function(c) {
          c.style.display = 'none';
        });
        waiterElements.forEach(function(w) {
          w.style.display = 'block';
        });
      }
    });
  });
  

//Метод кнопок вызова официанта и попросить счёт
const button_call_waiter = document.querySelectorAll('.button_call_waiter');
const button_score = document.querySelectorAll('.button_score');
const messageContainer = document.getElementById('waiterMessage');

button_call_waiter.forEach(button => {
    button.addEventListener('click', () => {
        const urlParams = new URLSearchParams(window.location.search);
        const tableNum = urlParams.get('table');
        const requestBody = {
            table_number: tableNum,
            call_waiter: true
        };

        const xhr = new XMLHttpRequest();
        xhr.open('POST', 'https://100.127.212.49:5001/waiter');
        xhr.setRequestHeader('Content-Type', 'application/json');
        xhr.onreadystatechange = function () {
            if (xhr.readyState === 4 && xhr.status === 200) {
                console.log('Успешно вызван официант');
                messageContainer.textContent = 'Официант скоро будет у вас'; // Установка текста в элемент
                messageContainer.style.display = 'block'; // Отображаем элемент с сообщением
            
                setTimeout(function() {
                    messageContainer.style.display = 'none'; // Скрываем сообщение через несколько секунд
                }, 6000); // Установите таймер для скрытия сообщения через 3 секунды
            }
        }
        xhr.send(JSON.stringify(requestBody));
    });
});

button_score.forEach(button => {
    button.addEventListener('click', () => {
        const urlParams = new URLSearchParams(window.location.search);
        const tableNum = urlParams.get('table');
        const requestBody = {
            table_number: tableNum,
            score: true
        };

        const xhr = new XMLHttpRequest();
        xhr.open('POST', 'https://100.127.212.49:5001/waiter');
        xhr.setRequestHeader('Content-Type', 'application/json');
        xhr.onreadystatechange = function () {
            if (xhr.readyState === 4 && xhr.status === 200) {
                console.log('Успешно запросили счёт');
                messageContainer.textContent = 'Официант скоро принесет счёт'; // Установка текста в элемент
                messageContainer.style.display = 'block'; // Отображаем элемент с сообщением
            
                setTimeout(function() {
                    messageContainer.style.display = 'none'; // Скрываем сообщение через несколько секунд
                }, 6000); // Установите таймер для скрытия сообщения через 3 секунды
                
            }
        }
        xhr.send(JSON.stringify(requestBody));
    });
});
